import React, { useState } from 'react';

const Contact = () => {

    const [data, setdata] = useState({
        fullname : "",
        phone : "",
        email : "",
        messsage : ""
    });

    const formSubmit = (e) =>
    {
        e.preventDefault();
        alert(`Name: ${data.fullname}, Phone No: ${data.phone}, Email: ${data.email} and Message: ${data.messsage}.`)
    }

    const inputEvent = (event) =>
    {
        const  {name , value} = event.target;
        setdata( (preVal) => {
            return{
            ...preVal,
            [name]: value,
            };
        });
    }

    return (
        <>
            <div className = "my-5">
                <h1 className = "text-center"> Contact Us</h1>
            </div>
            <div className = "container conntact-div">
                <div className="row">
                    <div className = "col-md-6 col-10 mx-auto">
                        <form onSubmit = {formSubmit}>
                            <div className ="mb-3">
                                <label for="exampleFormControlInput1" className ="form-label">Full Name</label>
                                <input type="text" className ="form-control" id="exampleFormControlInput1" name ="fullname" value = {data.fullname} onChange = {inputEvent} placeholder="Enter Your Name" />
                            </div>
                            <div className ="mb-3">
                                <label for="exampleFormControlInput1" className ="form-label">Phone Number</label>
                                <input type="number" className ="form-control" id="exampleFormControlInput1" name ="phone" value = {data.phone} onChange = {inputEvent} placeholder="Phone Number" />
                            </div>
                            <div className ="mb-3">
                                <label for="exampleFormControlInput1" className ="form-label">Email address</label>
                                <input type="email" className ="form-control" id="exampleFormControlInput1" name ="email" value = {data.email} onChange = {inputEvent} placeholder="name@example.com" />
                            </div>
                            <div className ="mb-3">
                                <label for="exampleFormControlTextarea1" className ="form-label">Message</label>
                                <textarea className ="form-control" id="exampleFormControlTextarea1" rows="3" name ="messsage" value = {data.messsage} onChange = {inputEvent}></textarea>
                            </div>
                            <div className = "col-12">
                                <button className = "btn btn-outline-primary" type = "submit"> Submit Form</button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </>
    );
};

export default Contact;
